
.input-group>span {
    font-size: 0.85rem !important;
}
.my-dropdown {
    display: inline-block;
    position: relative;
}
.my-dropdown .btn:focus,
my-dropdown .btn:active {
    outline: none !important;
}
.my-dropdown-menu {
    position: absolute;
    left: 0;
    top: 38px;
    background-color: #fff;
    width: 100%;
    line-height: 200%;
    z-index: 5;
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.my-dropdown-item {
    cursor: pointer;
    padding-left: .75rem;
}
.my-dropdown-item:hover {
    background: #ddd;
}



