
.card-link {
    color: #007bff !important;
}
.card-link:hover,
.page-link {
    cursor: pointer;
}
.list-group-item:first-child,
.list-group-item:last-child {
    border-radius: 0 !important;
}
.facet-result {
    cursor: pointer;
}
.facet-count {
    text-align: right;
}
.list-group-item:focus,.list-group-item:active {
   outline: none !important;
}



